import React from 'react';
import textlogo from './../logo/textlogo.svg';
import continueRule from './../logo/continueRule.svg';
import facebook from './../logo/facebook.svg';
import google from './../logo/google.svg';
import cancel from './../logo/cancel.svg';

const Signup = ()=>{
	const hello = ()=>{
		console.log('hello');

					
	}
	return(
		   <>
			<div className="modal fade" id="signupModal" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
			  <div className="modal-dialog">
			    <div className="modal-content container">
			      <div className="mt-5 border-0">
			        <div className="logo-wrapper">
			            <div className="textlogo px-3 me-5">
			        	  <img src={textlogo} alt="jeetar logo" />
			        	</div>
			        	<div className="already-sign-in-wrapper ms-5">
			        	  <p className="already-sign-in pt-2">Already have an Account? <a href="#" className="text-primary signin"> Sign In</a></p>
			        	</div>
			        </div>
			        <img src={cancel} type="button" className="close" alt="cancel" data-bs-dismiss="modal" aria-label="Close" />
			      </div>
			      <div className="modal-body mt-n5">
			        <p className="sign-up">Sign Up For Free, Now</p>

			        <form  onSubmit={hello} needs-validation="true" className="needs-validation" >
			           <div className="row g-2">
			              <div className="col-3">
			           	    <select className="form-select form-field">
			           	      <option value="+234">+234</option>
			           	    </select>
			              </div>
			              <div className="col-9">
			           	    <input type="text" className="form-control form-field" placeholder="Phone Number"/>
			              </div>
			           </div>
			            <div className="col-12 form-group">
			           	  <input type="text" className="form-control form-field" placeholder="Full Name"/>
			           </div>
			           <div className="col-12 form-group">
			           	  <input type="text" className="form-control form-field" placeholder="E-mail (Optional)"/>
			           </div>
			           <div className="col-12 form-group">
			           	  <input type="password" className="form-control form-field" placeholder="Password"/>
			           </div>
			           <div className="col-12 form-group">
			           	  <input type="password" className="form-control form-field" placeholder="Re-Type Password"/>
			           </div>
			           <div className="col-12 form-group">
			           	  <p className="password-notes ml-3"><span className="text-danger">*</span> Password must be at least 7 long and include at least one special character.</p>
			           </div>
			           <div className="col-12 form-group">
					    <div className="form-group form-check">
					      <input className="form-check-input" type="checkbox" value="" id="invalidCheck" />
					      <label className="form-check-label">
					        I want to be informed of special promotions and news regarding jeetar
					      </label>
					       <div className="invalid-feedback">
					        You must agree before submitting.
					      </div>
					    </div>
					  </div>
					   <div className="col-12 form-group">
			           	  <button type="submit" className="submit form-control btn-primary">Create Account</button>
			           </div>
			           <div className="col-12 form-group">
			              <img src={continueRule} className="d-block mx-auto" />
			           </div>
			           <div className="col-12 form-group text-center">
			              <a href="#"><img src={google} className="mx-3" alt="google"/></a>
			              <a href="#"><img src={facebook} className=" mx-3" alt="facebook"/></a>
			           </div>

			        </form>
			         
			      </div> 
			  
			    </div>
			  </div>
			</div>
		   </>
		);
}

export default Signup