import React from 'react';
import textlogo from './../logo/textlogo.svg';
import jeetaryellow from './../img/jeetaryellow.svg';
import user from './../logo/user.svg';

const Navbar = ()=>{
	return(
		   <nav className="navbar navbar-expand-lg shadow-sm text-white">
			  <div className="container-fluid">
			    <a className="navbar-brand p-3 me-5 active" href="#" >jeetar</a>
			    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
			      <span className="navbar-toggler-icon"></span>
			    </button>
			    <div className="collapse navbar-collapse" id="navbarSupportedContent">
			      <ul className="navbar-nav me-auto mb-2 mb-lg-0 nav-left">
			         <li className="nav-item me-3">
			          <a className="nav-link" href="#">jeetarhealth</a>
			        </li>
			         <li className="nav-item">
			          <a className="nav-link" href="#">jeetarbusiness</a>
			        </li>
			        
			      </ul>

			      <ul className="navbar-nav ml-auto mb-2 mb-lg-0 nav-right">
			        <li className="nav-item">
			          <a className="nav-link me-5" href="#">Login</a>
			        </li>
					<li className="nav-item">
			          <a className="nav-link sign-up-btn" href="#" data-bs-toggle="modal"  data-bs-target="#signupModal"> <img src={user} /> Sign Up</a>
			        </li>

			      </ul>
			      
			    </div>
			  </div>
			</nav>
		);
}

export default Navbar